import React, { Component } from 'react';
import { connect } from 'react-redux';
import SocketService from '../services/SocketService';
import Comments from '../cmps/Comments';
import { addComment, loadComments } from '../actions/CommentActions';
import { loadPosts, likeUnkikePost, addPost, removePost } from '../actions/PostActions.js';
import PostOptionsModal from '../cmps/PostOptionsModal'
import ScrollToTop from '../cmps/ScrollToTop'
import UserService from '../services/UserService';
// import Like from '../cmps/Like'
import '../assets/css/PostPage/PostPage.css'
import utils from '../utils'
class PostPage extends Component {
    state = {
        comments: [],
        createdAt: '',
        user: null
    }
    async componentDidMount() {
        const { post, user } = this.props.location.state
        this.getTimeElapsed(post.createdAt)
        if (this.state.comments)
            this.props.loadComments(post._id)
                .then((comments) => {
                    this.setState({ comments: comments, user: user })
                })

        SocketService.setup();
        SocketService.emit('post', post._id);
        SocketService.on('post addComment', this.addComment);
    }
    componentWillUnmount() {
        SocketService.off('post addComment', this.addComment);
        SocketService.terminate();
    }

    getTimeElapsed = async (createdAt) => {
        var time = await utils.getTimeElapsed(createdAt)
        this.setState({ createdAt: time })
    }

    addComment = (newComment) => {
        this.setState(prevState => ({ comments: [...prevState.comments, newComment] }));
    };

    sendComment = async (txt) => {
        let postId = this.props.location.state.post._id

        var date = new Date();
        var timestamp = date.getTime();

        var owner = (this.props.loggedInUser) ? this.props.loggedInUser
            : await UserService.getById('5e5796c01c9d44000049191b')


        var commentObj = {
            _id: '',
            createdAt: timestamp,
            txt: txt,
            owner: owner
        }
        const newComment = await this.props.addComment(commentObj, postId)//mongoDB adds ObjectId for key in map
        SocketService.emit('post newComment', newComment);
    };

    postOptionsClicked = (postId) => {
        var bg = document.querySelector(`.options-modal-${postId}, options-modal-bg`)
        var modal = document.querySelector(`.post-options-modal-${postId}, post-options-modal`)

        if (!this.state.isOptsModalOpen) {
            bg.classList.remove('hidden')
            modal.classList.remove('hidden')
            modal.classList.add('scale-in-hor-center')
            modal.classList.remove('scale-out-horizontal')
            this.setState({ isOptsModalOpen: true })
        } else {
            modal.classList.add('scale-out-horizontal')
            modal.classList.remove('scale-in-hor-center')
            setTimeout(() => {
                modal.classList.add('hidden')
                bg.classList.add('hidden')
            }, 300);
            this.setState({ isOptsModalOpen: false })
        }
    }
    onRemovePost = async (postId) => {
        await this.props.removePost(postId)
        this.props.history.push({
            pathname: `/`
        });
    }

    render() {
        const { post, user } = this.props.location.state
        const { comments } = this.state
        const { loggedInUser, likeUnkikePost } = this.props
        return (
            <div className="b-post-container flex">

                <img className="b-post-img" src={post.imgUrl} alt="post pic"></img>
                <div className="b-post-body">

                    <div className="b-post-header-inner-container flex col">
                        <div className="b-post-header flex row">
                            <div>
                                <img className="b-post-header-owner-img" alt="post owner pic" src={post.owner.imgUrl}></img>
                                <span className="b-post-header-owner-nickname">{post.owner.username}</span>
                            </div>
                            <img className="post-header-options-btn" onClick={() => this.postOptionsClicked(post._id)}
                                src={require('../assets/img/options.png')} alt="options pic" title="options"></img>
                        </div>
                        <br />
                        {<div className="post-buttons-container flex row">
                            {/* {<Like btnClassName={'p-post-btn'} post={post} user={user} loggedInUser={loggedInUser} likeUnkikePost={likeUnkikePost} />} */}
                            {/* <img className="post-btn" onClick={() => this.toPost(post._id, post, user)} alt="see post" title="see post" src={require('../assets/img/comment.png')}></img> */}
                            {/* <img className="post-btn" alt="share" title="share" onClick={() => shareClicked(post._id)} src={require('../assets/img/share.png')}></img> */}
                        </div>}
                        <span className="postpage-post-create-date" >{this.state.createdAt}</span>
                    </div>

                    <section className="b-post-comments-container">
                        <div className="b-post-comments-wrapper">
                            <div className="">
                                <Comments sendComment={this.sendComment} comments={comments} loggedInUser={loggedInUser} />
                            </div>
                        </div>

                    </section>
                </div>
                <PostOptionsModal postId={post._id}>
                    <div className={`post-options-modal-${post._id} post-options-modal flex col`} >
                        <img className={`close-modal-img-btn`} onClick={() => this.postOptionsClicked(post._id)} src="https://res.cloudinary.com/ilya432/image/upload/v1581638014/close_exlrw7.png" alt="close modal" title="close"></img>

                        {loggedInUser && (post.owner._id === loggedInUser._id) && <span className={`post-opt`} postid={post._id} onClick={() => this.onRemovePost(post._id)}>delete</span>}
                        <span className={`post-opt`} postid={post._id}>share</span>
                    </div>
                </PostOptionsModal>
                <ScrollToTop className="scroll-to-top" />
            </div>
        );
    }
}


const mapStateToProps = state => {
    return {
        loggedInUser: state.user.loggedInUser,
        // comments: state.comment.comments
    };
};
const mapDispatchToProps = {
    addComment,
    loadComments,
    loadPosts, likeUnkikePost, addPost, removePost,
};

export default connect(mapStateToProps, mapDispatchToProps)(PostPage);


