import React from 'react';
const blank = require('../assets/img/like-blank.png')
const liked = require('../assets/img/like-active.png')

export default class PostAdd extends React.Component {

    state = {
        likeImgSrc: blank,
        isLiked: false
    }
    onLikeClicked = (post, user, isLiked) => {

        // console.log('postId: ' + post._id);
        // console.log('userName: ' + user.username);
        if (!isLiked) {
            try {
                this.props.likeUnkikePost(post, user, isLiked)
                    .then(() => {
                        this.setState({ likeImgSrc: liked, isLiked: true })
                    })
            } catch (err) {
                console.log('onLikeClicked -> error in likePost: ', err)
            }

        }
        else if (isLiked) {
            try {
                this.props.likeUnkikePost(post, user, isLiked)
                    .then(() => {
                        this.setState({ likeImgSrc: blank, isLiked: false })
                    })
            } catch (err) {
                console.log('onLikeClicked -> error in likePost: ', err)
            }
        }
    }

    checkLikeState = (post, user) => {
        post.likedBy.forEach(liker => {
            if (liker._id === user._id) {
                this.setState({ likeImgSrc: liked, isLiked: true })
            }
        });

    }

    render() {
        const { post, user, btnClassName } = this.props
        const { isLiked, likeImgSrc } = this.state
        return (
            <img className={btnClassName} alt="like" title="like" onLoad={() => this.checkLikeState(post, user)}
                onClick={() => this.onLikeClicked(post, user, isLiked)} src={likeImgSrc}>
            </img>
        );
    }
}